import React from "react";
import styles from './GuideBad.module.css';
import {ReactComponent as Fogi} from './fogi.svg';

const GuideBad = () =>{
    return(
        <>
            <div className={`${styles.Guide}`}>
                <h1 className={`${styles.GuideTitle}`}> </h1>
                <h1 className={`${styles.GuideDetail}`}>   : <span className={`${styles.stat1234}`} ></span></h1>
                <h1 className={`${styles.GuideDetailTitle}`}>    .</h1>
                <div className={`${styles.GuideStandard}`}>
                <h1>-    KF80, KF94 .</h1>
                <h1>-       .</h1>
                <h1>-   .</h1>
                </div>
            </div>
            <Fogi className={`${styles.Fogi}`} />
        </>
    )
}

export default GuideBad;