import React from "react";
import styles from './Bad.module.css';
import {ReactComponent as Bad}  from '../standard/Bad.svg';

const D2Bad = ({area}) => {
        /*pm25Value = , no2Value = , coValue = ,
        o3Value =  so2Value   */
    return(
        <>
            <div className={`${styles.DustStateSection}`}>
                <h1 className={`${styles.Title}`}>'{area.areaName || ' !'}'  </h1>
                <div className={`${styles.DustStateSectionTitle}`}>
                    <h1><Bad />
                    <br/>
                    <span className={`${styles.State}`}></span>
                </h1>
                    <h1 className={`${styles.h2}`}><br />(PM-2.5)</h1>
                    <h1 className={`${styles.BadAmount}`}>{area.areaData.pm25Value ||'---'}/</h1>
                </div>
                <div className={`${styles.SubTitle}`}>
                    <div>
                        <h2 className={`${styles.h2}`}>(NO)</h2>
                        <h2 className={`${styles.last}`}>{area.areaData.no2Value ||'---'}/</h2>
                    </div>
                    <div>
                        <h2 className={`${styles.h2}`}>(CO)</h2>
                        <h2 className={`${styles.last}`}>{area.areaData.coValue ||'---'}ppm</h2>
                    </div>
                    <div>
                        <h2 className={`${styles.h2}`}>(O)</h2>
                        <h2 className={`${styles.last}`}>{area.areaData.o3Value ||'---'}ppm</h2>
                    </div>
                    <div>
                        <h2 className={`${styles.h2}`}>(SO)</h2>
                        <h2 className={`${styles.last}`}>{area.areaData.so2Value ||'---'}ppm</h2>
                    </div>
                </div>
                <span className={`${styles.warning}`}>*      <span className={`${styles.ev}`}></span>  ,
                <br />    .</span>
            </div>
        </>
    )
}

export default D2Bad;