import React from "react";
import styles from './GuideGood.module.css';
import {ReactComponent as Forest}  from '../guideline/forest.svg';

const GuideGood = () =>{
    return(
        <>
            <div className={`${styles.Guide}`}>
                <h1 className={`${styles.GuideTitle}`}> </h1>
                <h1 className={`${styles.GuideDetail}`}>   : <span className={`${styles.GuideSpan}`}></span></h1>
                <h1 className={`${styles.GuideLast}`}>   !</h1>
            </div>
            <Forest className={`${styles.Forest}`}/>
        </>
    )
}

export default GuideGood;