import React from "react";
import styles from './GuideNomal.module.css';
import {ReactComponent as Forest2}  from '../guideline/forest2.svg';

const GuideNomal = () =>{
    return(
        <>
            <div className={`${styles.Guide}`}>
                <h1 className={`${styles.GuideTitle}`}> </h1>
                <h1  className={`${styles.GuideDetail}`}>   : <span className={`${styles.stat1234}`}></span></h1>
                <h1 className={`${styles.GuideDetailTitle}`}>  !</h1>
                <div className={`${styles.GuideStandard}`}>
                    <h1  className={`${styles.tt}`}>     !</h1>
                    <br/>
                    <h1>*: , , , </h1>
                </div>
            </div>
            <Forest2 className={`${styles.Forest2}`} />
        </>
    )
}

export default GuideNomal;