import React from "react";


const Time = () => {
    const nowTime = () => {
        let now = new Date();
        let nowYear = now.getFullYear();
        let nowMonth = now.getMonth() + 1;
        let nowDay = now.getDate();
        let hours = now.getHours();

        return nowYear + ' ' + nowMonth + ' ' + nowDay + ' ' + hours + ''
    }

    return (
        <>{nowTime()}</>
    );
};

export default Time;